.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: "ChildWitch";
  src: local("ChildWitch"), url(./assets/ChildWitch.ttf) format("truetype");
}

.header {
  font-family: ChildWitch;
  font-size: 6.5rem;
  color: #c97f1e;
  text-shadow: 2px 2px 4px #000000;
}

body {
  background: url("./assets/images/bg4.png") no-repeat center fixed;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-repeat: no-repeat;
  height: auto !important;
  min-height: 100vh;
}

/* BOARD/CARDS */
.board {
  width: 80%;
  /* background: rgb(255, 255, 255, 0.75); */
  /* background: rgb(53, 56, 62, 0.75); */
  background: rgb(188, 194, 221, 0.35);
  /* align-items: center;
  justify-content: center;
  display: flex; */
  border-radius: 12px;
}

.board > div {
  margin-left: 8%;
  margin-right: 3.5%;
  padding-bottom: 6%;
  padding-top: 3%;
}

.card img {
  border-radius: 13px;
  object-fit: cover;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.card {
  max-width: 150px;
  max-height: 150px;
  aspect-ratio: 1/1;
  border-color: none;
  padding: 0px;
  border: none;
}

.back {
  border-radius: 14px;
  border-color: #35383e !important;
  border-width: 2px;
}

.css-a5rdam-MuiGrid-root > .MuiGrid-item {
  padding-top: 0px !important;
}

.lives {
  font-family: "Saira Condensed", sans-serif;
  /* display: inline; */
}

/* END OF GAME MODAL */
.endMsg {
  font-size: 1.65rem !important;
  line-height: 1 !important;
  text-align: center;
}

.css-ys1sto {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 400px;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0px 11px 15px -7px rgba(0, 0, 0, 0.2),
    0px 24px 38px 3px rgba(0, 0, 0, 0.14), 0px 9px 46px 8px rgba(0, 0, 0, 0.12);
  outline: none;
  padding: 32px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
}

.css-1k23hlb-MuiButtonBase-root-MuiButton-root {
  background-image: linear-gradient(
    to right,
    #c57600 0%,
    #f09819 51%,
    #c97f1e 100%
  ) !important;
  padding: 7px 35px !important;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white !important;
  box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  border: none !important;
}

.css-1k23hlb-MuiButtonBase-root-MuiButton-root:hover {
  background-position: right center;
  color: #fff;
  text-decoration: none;
}

/* MEDIA QUERIES */
@media screen and (max-width: 768px) {
  .header {
    font-size: 4.5rem;
    padding-top: 1rem;
  }

  .board {
    width: 100%;
  }
}

@media screen and (max-width: 390px) {
  .header {
    font-size: 3.5rem;
    padding-top: 1.5rem;
  }

  .board {
    width: 100%;
    margin-top: 1.75rem;
  }

  .board > div {
    margin-left: 3%;
  }
  .container {
    padding-right: (--bs-gutter-x, 0.25rem) !important;
    padding-left: var(--bs-gutter-x, 0.25rem) !important;
  }
}
